import React, { Component } from 'react';
import axios from 'axios';
import Translate from "../Components/local/Translate";

class dataTable extends Component {
    constructor(props) {
        super(props);

        this.state = {
            hits: [],

        };
    }

    componentDidMount() {
        const i = this.props.idEstabliment;
        this.setState({ isLoading: true });

        const url2 = 'https://api.restaurat.me/controller/reserves/tableHorari.php?id=' + i;
        axios.post(url2).then(response => response.data)
            .then((data) => {
                this.setState({ hits: data })
            })
    }

    comprovaDia(param){
        switch(param) {
            case 1:
              return 'Diumenge';
            case 2:
                return 'Dilluns';
            case 3:
                return 'Dimarts';
            case 4:
                return 'Dimecres';
            case 5:
                return 'Dijous';
            case 6:
                return 'Divendres';
            case 7:
                return 'Dissabte';
          }

    }
    render() {
        const { hits } = this.state;
        return (
            <div className="App">
                <div className="container">
                    <table className="table table-striped table-bordered ">
                        <thead>
                            <tr>
                                <th><Translate string={'dia'}/></th>
                                <th><Translate string={'obertura'}/></th>
                                <th><Translate string={'tancament'}/></th>
                            </tr>
                        </thead>
                        <tbody>
                            
                            {hits.map(function (item) {
                            return (
                                <tr>
                                   <td>{ item.dia }</td> 
                                    <td>{item.horari_obertura}</td>
                                    <td>{item.horari_tancament}</td>
                                </tr>
                            )
                        })}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}


export default dataTable;